import { Component, OnInit } from '@angular/core';
import {
  UntypedFormBuilder,
  UntypedFormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { UsuarioService } from 'src/app/services/auth/usuario.service';

import { LayoutService } from 'src/app/layout/service/app.layout.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styles: [
    `
      :host ::ng-deep .pi-eye,
      :host ::ng-deep .pi-eye-slash {
        transform: scale(1.6);
        margin-right: 1rem;
        color: var(--primary-color) !important;
      }
    `,
  ],
})
export class LoginComponent implements OnInit {
  // valCheck: string[] = ['remember'];

  // password!: string;

  constructor(
    public layoutService: LayoutService,
    private fb: UntypedFormBuilder,
    private router: Router,
    private usuarioService: UsuarioService,
    private messageService: CustomMessageService,
  ) {}

  myForm: UntypedFormGroup = this.fb.group({
    username: ['', [Validators.required]],
    password: ['', [Validators.required]],
  });

  ngOnInit(): void {
    this.usuarioService.removeStorage();
    localStorage.removeItem('recarga');
  }

  login() {
    if (this.myForm.invalid) {
      this.myForm.markAllAsTouched();
      this.messageService.add({
        severity: 'info',
        summary: 'Por favor.',
        detail: 'Revisa que no tengas errores en los campos.',
      });
      return;
    }

    this.usuarioService.login(this.myForm.value).subscribe(
      (resp) => {
        this.messageService.add({
          severity: 'success',
          summary: 'Logueado',
          detail: 'Usted ha entrado exitosamente al sistema',
        });
        localStorage.setItem('username', `${this.myForm.value.username}`);
        this.router.navigateByUrl('/');
      },
      (error) => {
        if (error.status == 401) {
          this.messageService.error(error.error.detail)
          return;
        }
        if (error.status == 403) {
          this.messageService.error(error.error.detail)
          return;
        }
      },
    );
  }

  campoValido(campo: string) {
    return (
      this.myForm.controls[campo].errors && this.myForm.controls[campo].touched
    );
  }
}
